@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@400;700&display=swap);
*{
    margin: 0%;
    padding: 0%;
    box-sizing: border-box;
    font-family: 'Work Sans', sans-serif;
}
.nav-bar-container {
  background: #ffd4a3;
  width: 100%;
}

.nav-bar {
  max-width: 1080px;
  margin: auto;
  display: flex;
  padding: 12px 20px;
  justify-content: space-between;
  align-items: center;
}

.nav-bar button {
  border: 0px;
  border-radius: 0px;
  background: none;
  cursor: pointer;
}

.left-nav {
  flex: 1 1;
}

.right-nav {
  flex: 1 1;
}

.menu-bar {
  width: 100%;

  display: flex;
  align-items: center;
  justify-content: space-between;
}

.menu-bar > ul {
  list-style: none;
  display: flex;
}
.menu-bar > ul > li {
  padding: 4px 20px;
}

.three-Bars i {
  font-size: 20px;
  display: none;
}

.menu-bar > input {
  display: none;
}

@media only screen and (max-width: 740px) {
  .menu-bar {
    justify-content: flex-end;
    position: relative;
  }
  .three-Bars i {
    font-size: 20px;

    display: block;
  }

  .menu-bar > ul {
    background: #ffd4a3;
    position: absolute;
    top: 32px;
    height: 0px;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    width: 200%;
    overflow: hidden;
    transition: height 0.5s;
  }

  .menu-bar > input:checked ~ ul {
    height: 106px;
  }

  .menu-bar > ul > li {
    width: 100%;
    text-align: center;
    border-radius: 4px;
  }
}

.UpperPage-Container {
  width: 100%;
  background-image: linear-gradient(to right, #ffe1ba, #fff1d5);
}

.UpperPage {
  max-width: 1080px;
  margin: auto;
  display: flex;
  padding: 12px 20px;
  justify-content: space-between;
  align-items: center;
}

.UpperPage-left {
  flex: 1 1;
}

.UpperPage-left > h1 {
  width: 250px;
}

.UpperPage-left > p {
  width: 300px;
}

.seeMore {
  background: none;
  border: 0px;
  font-size: 8px;
  padding: 0px 16px;
  cursor: pointer;
}

.UpperPage-right {
  width: 100%;
  flex: 1 1;
  display: flex;
  justify-content: flex-end;
}

.UpperPage-right > img {
  width: 90%;
  height: auto;
}

@media only screen and (max-width: 760px) {
  .UpperPage {
    flex-direction: column-reverse;
  }

  .UpperPage > .UpperPage-left > h1 {
    font-size: 24px;
  }

  .UpperPage-left > p {
    font-size: 12px;
  }
}

@media screen and (max-width: 420px) {
  .UpperPage-left > p {
    display: none;
  }
}

.button1 {
  padding: 4px 12px;
  font-size: 8px;
  border-radius: 12px;
  border: 0px;
  background: #323430;
  color: aliceblue;
  cursor: pointer;
}

.LowerPage1-Container {
  width: 100%;
  background: #fefce3;
}

.LowerPage1 {
  max-width: 1080px;
  margin: auto;
  display: flex;
  flex-direction: column;
  padding: 50px 20px;
  align-items: center;
  grid-gap: 40px;
  gap: 40px;
}

.LowerPage1-up {
  text-align: center;
  width: 100%;
}

.LowerPage1-down {
  width: 100%;
}

.card-container {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  grid-gap: 12px;
  gap: 12px;
  flex-wrap: wrap;
}

.card-body {
  width: 250px;
  height: 250px;
  border-radius: 24px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  grid-gap: 8px;
  gap: 8px;
  padding-bottom: 20px;
  position: relative;
}

.card-body > button {
  width: 80px;
  padding: 4px;
}

.card-image {
  width: 80%;
  height: 150px;
  position: absolute;
  z-index: 1;
  top: -15%;
  text-align: center;
}

.card-image > img {
  width: 50%;
  height: 100%;
}

@media only screen and (max-width: 920px) {
  .card-container {
    grid-gap: 50px;
    gap: 50px;
  }
}

@media only screen and (max-width: 770px) {
  .card-container {
    grid-gap: 50px;
    gap: 50px;
  }
}

@media only screen and (max-width: 480px) {
  .card-container {
    grid-gap: 50px;
    gap: 50px;
  }
}

.lowerPage2-container {
  width: 100%;
  height: auto;
}

.lowerPage2 {
  width: 100%;
  height: auto;
}

.item {
  width: 100%;
  padding: 50px 0px;

  display: flex;
  flex-wrap: wrap;
}

.item-oud {
  flex-direction: row;
  background: #ecceb2;
}

.item-even {
  flex-direction: row-reverse;
  background: #fefccb;
}

.item-left {
  flex: 1 1;
  height: auto;

  display: flex;
  justify-content: center;
  align-items: center;
}

.item-image {
  margin: 0px 20px;
  border: 1px solid gray;
  border-radius: 8px;
  width: 300px;
  height: 300px;
  display: flex;
  align-items: center;
}
.item-image > img {
  width: 100%;
}

.item-right {
  flex: 1 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.item-details {
  border-radius: 8px;
  width: 300px;
  height: 300px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  padding: 50px 0px;
  margin: 0px 20px;
}

.item-details > p {
  font-size: 12px;
}

.item-details > button {
  width: 80px;
}

.lowerPage3-container {
  width: 100%;
  height: auto;
  background: #fefce3;
}

.lowerPage3 {
  max-width: 1080px;
  width: 100%;
  margin: auto;
  display: flex;
  padding: 50px 20px;
  justify-content: center;
}

.lowerPage3-content {
  border: 1px solid gray;
  border-radius: 8px;
  width: 50%;
  max-width: 500px;
  min-width: 300px;
  padding: 20px 36px;
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 12px;
  gap: 12px;
}

.lowerPage3-content > p {
  font-size: 12px;
  width: 80%;
  text-align: center;
}
.lowerPage3-content > button {
  width: 80px;
}

.footer-container {
  width: 100%;
  background: rgb(63, 60, 60);
}

.footer {
  color: #fefce3;
  max-width: 1080px;
  width: 100%;
  margin: auto;
  padding: 50px 20px;
  display: flex;
  justify-content: space-between;

  flex-wrap: wrap;
}
.footer h5 {
  margin-bottom: 12px;
}

.footer-column-1,
.footer-column-2,
.footer-column-3,
.footer-column-4 {
  padding: 20px 12px;
}

.footer ul {
  list-style: none;
  font-size: 12px;
  display: flex;
  flex-direction: column;
  grid-gap: 4px;
  gap: 4px;
}

@media only screen and (max-width: 840px) {
  .footer-column-1,
  .footer-column-2,
  .footer-column-3,
  .footer-column-4 {
    min-width: 250px;
  }
}

@media only screen and (max-width: 720px) {
  .footer {
    justify-content: center;
  }
}

@media only screen and (max-width: 480px) {
  .footer-column-1,
  .footer-column-2,
  .footer-column-3,
  .footer-column-4 {
    text-align: center;
  }
}


